import React, {useState} from 'react'
import { withRouter, Link } from 'react-router-dom'


export default function Navbar(){
    const [navBackground, setNavBackground] = useState(false);
    console.log(navBackground);

    document.addEventListener('scroll', e => {
        let scroll = window.scrollY;
        setNavBackground(scroll >= 450);
    });

    return(
        <nav className={navBackground ? 'nav-background' : ''}>
            <h3><Link className="button" to="/Aechtech">Archtech</Link></h3>
            <ul class="menu">
                <li><Link className="button" to="/Aechtech">O nas</Link></li>
                <li><Link className="button" to="/Aechtech/project">Projekty</Link></li>
                <li><Link className="button" to="/Aechtech/jak-dzialamy">Jak działamy</Link></li>
                <li><Link className="button" to="/Aechtech/kontakt">Kontakt</Link></li>
            </ul>
        </nav>
    )
}